    var card_name_publicwechat = '';
    var card_name_addr = '';
    var card_name_company = '';
    var card_name_note = '';
    var card_name_logo_image = '';
    var card_cele_title = '';
    var card_cele_content = '';
    var card_cele_logo = '';
    var card_love_title = '';
    var card_love_content = '';
    var card_love_logo = '';
    var card_love_related = '';
    var card_love_phone = '';
