    upimg: function (e) {
      var that = this;
      var is_logo = e.currentTarget.dataset.logo ? e.currentTarget.dataset.logo : 0;
      var new_img_arr = '';
      that.setData({
        is_logo: is_logo
      });
      wx.chooseImage({
        sizeType: ['original', 'compressed'],
        success: function (res) {
          that.setData({
            new_img_arr: res.tempFilePaths
          });
          console.log(':', that.new_img_arr, res.tempFilePaths);
          that.upload();
        }
      });
    },
    cancel_upimg: function (e) {
      var that = this;
      var is_logo = e.currentTarget.dataset.logo ? e.currentTarget.dataset.logo : 0;
      console.log('cancel_upimg:', is_logo);

      if (is_logo == 1) {
        that.setData({
          card_name_logo_image: ''
        });
      } else if (is_logo == 2) {
        that.setData({
          card_register_adv: ''
        });
      } else if (is_logo == 4) {
        that.setData({
          card_love_logo: ''
        });
      } else if (is_logo == 10) {
        that.setData({
          card_cele_logo: ''
        });
      }
    },
    upload: function () {
      var that = this;
      var goods_id = that.goodsid;
      var new_img_arr = that.new_img_arr[0]; //

      var image_pic = that.image_pic;
      var is_logo = that.is_logo;

      if (new_img_arr) {
        wx.uploadFile({
          url: uploadurl,
          filePath: new_img_arr,
          name: 'wechat_upimg',
          formData: {
            latitude: encodeURI(0.0),
            longitude: encodeURI(0.0),
            restaurant_id: encodeURI(0),
            city: encodeURI(''),
            prov: encodeURI(''),
            name: encodeURI(goods_id) // 

          },
          // HTTP  form data
          success: function (res) {
            var retinfo = JSON.parse(res.data.trim()); //console.log('upimg upload url:', retinfo['result']['img_url'])

            if (retinfo['status'] == "y") {
              if (is_logo == 1) {
                //logo 
                that.image_save(retinfo['result']['img_url'], 'card_name_logo_image');
                that.setData({
                  card_name_logo_image: retinfo['result']['img_url']
                });
              } else if (is_logo == 2) {
                that.setData({
                  card_register_adv: retinfo['result']['img_url']
                });
              } else if (is_logo == 4) {
                that.setData({
                  card_love_logo: retinfo['result']['img_url']
                });
              } else if (is_logo == 10) {
                that.setData({
                  card_cele_logo: retinfo['result']['img_url']
                });
              } else {
                var new_image_pic = [];
                that.image_save(retinfo['result']['img_url'], 'myregistercard_image');
                var myregistercard_image = wx.getStorageSync('myregistercard_image');
                var new_image_pic = {
                  id: goods_id,
                  goods_id: goods_id,
                  url: retinfo['result']['img_url']
                };
                image_pic.push(new_image_pic);
                setTimeout(function () {
                  that.setData({
                    image_pic: image_pic
                  });
                }, 3000);
                console.log(':', new_image_pic, image_pic);
              }
            } else {
              wx.showToast({
                title: '',
                icon: none,
                duration: 2000
              });
            }
          }
        });
      }

      wx.showToast({
        title: '',
        duration: 2000
      });
      var content = that.content;

      if (!content) {
        that.setData({
          content: ':'
        });
      }
    },
