	    title: ":"+recordPath,
	    icon: 'none',
	    duration: 1500
	  });
	  */
      wx.uploadFile({
        url: uploadurl,
        filePath: recordPath,
        name: 'wechat_upimg',
        formData: {
          latitude: encodeURI(0.0),
          longitude: encodeURI(0.0),
          restaurant_id: encodeURI(0),
          city: encodeURI(''),
          prov: encodeURI(''),
          name: encodeURI(goods_id) // 

        },
        success: function (res) {
          var retinfo = JSON.parse(res.data.trim());
          var new_rec_url = '';

          if (retinfo['status'] == "y") {
            new_rec_url = retinfo['result']['img_url'];
            that.setData({
              new_rec_url: new_rec_url,
			  
            });
            wx.setStorageSync('cardvoice', new_rec_url);
